import swings from "./static_root/images/g-swings-sm/*.jpg";

export interface Image {
  path: string;
  index: number;
}

export class Gallery {
  public id: number;
  public title: string;
  public description: string;
  public count: number;
  public images: Image[] = [];
  public coverImage = "";
  public galleryLink: string;
  public carouselId: string;
  public carouselIdTarget: string;
  public activated = ko.observable(true);

  constructor(
    id: number,
    title: string,
    images: any,
    count: number,
    description?: string
  ) {
    this.id = id;
    this.title = title;
    this.description = description || "";
    // this.path = path;
    this.count = count;
    this.galleryLink = "/gallery.html?id=" + this.id;
    this.carouselId = "crsl" + this.id;
    this.carouselIdTarget = "#" + this.carouselId;

    for (var i = 0; i < count; i++) {
      this.images.push({
        path: images[`${i + 1}`],
        index: i,
      });
    }
    this.coverImage = count >= 0 ? this.images[0].path : "";
  }
}

